}.mui-bar {
    position: fixed;
    z-index: 10;
    right: 0;
    left: 0;
    height: 44px;
    padding-right: 10px;
    padding-left: 10px;
    border-bottom: 0;
    background-color: #f7f7f7;
    -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, .85);
    box-shadow: 0 0 1px rgba(0, 0, 0, .85);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.mui-bar-tab {
    bottom: 0;
    display: table;
    width: 100%;
    height: 50px;
    padding: 0;
    table-layout: fixed;
    border-top: 0;
    border-bottom: 0;
    -webkit-touch-callout: none;
}

.mui-bar-tab .mui-tab-item {
    display: table-cell;
    overflow: hidden;
    width: 1%;
    height: 50px;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #929292;
}

.mui-bar-tab .mui-tab-item.mui-active {
    color: #ffc428;
}

.mui-bar-tab .mui-tab-item .icomoon_font {
    top: 3px;
    width: 24px;
    height: 24px;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 24px;
}

.mui-bar-tab .mui-tab-item .icomoon_font~.mui-tab-label {
    font-size: 11px;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
}

.mui-bar-tab .mui-tab-item .icomoon_font:active {
    background: none;
}

.mui-bar .mui-icon {
    font-size: 24px;
    position: relative;
    z-index: 20;
    padding-top: 10px;
    padding-bottom: 10px;
}

.mui-bar .icomoon_font:active {
    opacity: .3;
}

.mui-bar .mui-btn .icomoon_font {
    top: 1px;
    margin: 0;
    padding: 0;
}

.mui-bar .mui-title .icomoon_font {
    margin: 0;
    padding: 0;
}

.mint-indicator-wrapper {
    z-index: 999;
}

